nav {
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    /* border-radius: 3rem; */
  }
  
  nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 20% 90% 90% 90%;
    display: flex;
    color: rgb(0, 229, 255);
    font-size: 1.1rem;
    backdrop-filter: blur(25px);
  }
  
  nav a:hover {
    background: rgb(0, 229, 255);
    color: rgb(221, 2, 255);
    transform: rotateX(360deg);
    transition: var(--transition);
  }
  
  nav a.active {
    background: var(--color-primary);
    color: var(--color-bg);
  }