.pl {
    height: 100vh;
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color:rgb(22, 0, 36);
    border: 1px solid;
    border-image: linear-gradient(135deg, rgb(73, 220, 227), rgb(73, 220, 227), rgb(73, 220, 227), rgb(30, 0, 49), rgb(30, 0, 49), rgb(30, 0, 49)) 1;
}

.pl-title {
    font-size: 68px;
    font-style: italic;
    border-radius: 10px;
    font-weight: 200;
    color: rgb(221, 2, 255);
    text-shadow: -1px 1px 3px rgba(0, 0, 0, 0.415);
}

.pl-texts {
    font-size: 24px;
    font-weight: 200;
    width: 65%;
    color: rgb(73, 220, 227);
}


.pl-desc {
    margin: 20px 0px;
    font-size: 36px;
}

.pl-list {
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

@media screen and (max-width: 480px) {
    .pl {
        height: 100vh;
    }
    .pl-title {
        font-size: 48px;
    }
    .pl-desc {
        display: none;
    }
}

@media screen and (max-width: 428px) {
    .pl {
        height: 100%;
        /* height: 100vh; */
        padding: 25px 50px;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
    .pl-title {
        font-size: 28px;
    }
    .pl-list {
        width: 50vw;
    }
    
}